.descriptionbox {
    margin: 120px 170px;
}
.descriptionbox-navigator {
    display: flex;
    flex-wrap: wrap;
}
.descriptionbox-nav-box {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    font-weight: 600;
    width: 171px;
    height: 70px;
    border: 1px solid #d0d0d0;
}
.descriptionbox-nav-box.fade {
    background: #fbfbfb;
    color: #555;
}
.descriptionbox-description {
    display: flex;
    flex-direction: column;
    gap: 25px;
    border: 1px solid #d0d0d0;
    padding: 48px;
    padding-bottom: 70px;
}

/* 1280px */
@media (max-width: 1280px) {
    .descriptionbox {
        margin: 80px 60px;
    }
    .descriptionbox-nav-box {
        font-size: 14px;
        width: 150px;
        height: 60px;
    }
    .descriptionbox-description {
        padding: 30px;
        padding-bottom: 50px;
        font-size: 14px;
    }
}

/* 1024px */
@media (max-width: 1024px) {
    .descriptionbox {
        margin: 60px 30px;
    }
    .descriptionbox-nav-box {
        font-size: 13px;
        width: 130px;
        height: 55px;
    }
    .descriptionbox-description {
        padding: 25px;
        gap: 20px;
    }
}

/* 800px */
@media (max-width: 800px) {
    .descriptionbox {
        margin: 40px 20px;
    }
    .descriptionbox-nav-box {
        font-size: 12px;
        width: 120px;
        height: 50px;
    }
    .descriptionbox-description {
        padding: 20px;
        gap: 15px;
        font-size: 13px;
    }
}

/* 500px */
@media (max-width: 500px) {
    .descriptionbox {
        margin: 30px 10px;
    }
    .descriptionbox-nav-box {
        width: 100%;
        height: 45px;
        font-size: 11px;
    }
    .descriptionbox-description {
        padding: 15px;
        padding-bottom: 30px;
        font-size: 12px;
    }
}
