.footer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 50px;
    padding-top: 100px;
}
.footer-logo{
    display: flex;
    align-items: center;
    gap: 50px;
   
}
.footer-logo p{
    color: #383838;
    font-size: 46px;
    font-weight: 700;
}
.footer-links{
    display: flex;
    list-style: none;
    gap: 50px;
    color: #252525;
    font-size: 20px;
margin-left: 20px ;
}
.footer-links li{
    cursor: pointer;
}
.footer-socials-icons{
    display: flex;
    gap: 20px;
}
.footer-icons-container{
padding: 10px;
gap: 20px;
padding-bottom: 6px;
background: #fbfbfb;
border: 1px solid #ebebeb;
cursor: pointer;
}
.footer-copy-right{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    width: 100%;
    margin-bottom: 30px;
    color: #1a1a1a;
    font-size: 20px;
}
.footer-copy-right hr{
    width: 80%;
    border: none;
    border-radius: 10px;
    height: 3px;
    background: #c7c7c7;
}
@media (max-width:800px) {
    .footer{
        gap: 20px;
    }
    .footer-logo{
        gap: 10px;
        align-items: end;
    }
    .footer-logo img{
        width: 40px;
    }
    .footer-logo p{
        font-size: 25px;
    }
    .footer-links{
        font-size: 14px;
        gap: 10px;
        margin-right: 30px;
    }
    .footer-socials-icons img{
        width: 22px;
    }
    .footer-copy-right{
        font-size: 12px;
    }
}